<template>
    <div>
        <top-nav title="折扣活动列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1、 这里是为商品添加折扣活动</p>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="折扣ID">
                        <el-input v-model="searchForm.discount_id" placeholder="折扣ID"></el-input>
                    </el-form-item>

                    <el-form-item label="是否上线中">
                        <el-select v-model="searchForm.is_online" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="已下线" :value="0"></el-option>
                            <el-option label="上线中" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="PLUS会员特价">
                        <el-select v-model="searchForm.is_vip" placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option label="已下线" :value="0"></el-option>
                            <el-option label="上线中" :value="1"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="添加日期">
                        <date-between v-model="searchForm.adddate"></date-between>
                    </el-form-item>

                    <el-form-item label="开始日期">
                        <date-between v-model="searchForm.startdate"></date-between>
                    </el-form-item>

                    <el-form-item label="结束日期">
                        <date-between v-model="searchForm.enddate"></date-between>
                    </el-form-item>

                    <div style="padding-left:20px;">
                        <el-button size="mini" type="primary" @click="getList(1)">搜索</el-button>
                        <el-button size="mini" type="text" @click="searchForm.discount_id='',searchForm.goods_id='',searchForm.keywords='',
                        searchForm.is_online=-999,searchForm.adddate='',getList(1)">取消搜索</el-button>
                        <router-link to="/marketing/discount/create">
                            <el-button size="mini" type="danger">添加折扣</el-button>
                        </router-link>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column prop="discount_id" label="折扣ID" width="60"></el-table-column>

                    <el-table-column label="活动名称" width="180">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.name}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品名称">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.goods_name}} <span style="color: red">等 {{scope.row.goods_ids.length - 0}} 件商品</span>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="封面图片">
                        <template slot-scope="scope">
                            <img :src="scope.row.prom_img" width="100px;">
                        </template>
                    </el-table-column>

                    <el-table-column label="已售数量"  width="160">
                        <template slot-scope="scope">
                            {{scope.row.buy_num}}件
                        </template>
                    </el-table-column>

                    <el-table-column label="折扣有效期" width="200">
                        <template slot-scope="scope">
                            <p>开始时间: {{scope.row.start_time}}</p>
                            <p>结束时间: {{scope.row.end_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="折扣" width="150">
                        <template slot-scope="scope">
                            <p>注册会员：<b style="color: #D94854">{{scope.row.discount}}</b>  折</p>
                            <p>PLUS会员：<b style="color: #D94854">{{scope.row.vip_discount}}</b>  折</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="orderby" label="排序" width="100"></el-table-column>

                    <el-table-column label="操作日期/IP" width="200">
                        <template slot-scope="scope">
                            <p>日期：{{scope.row.add_time}}</p>
                            <p>IP：{{scope.row.add_ip}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn" label="活动开启" width="150">
                        <template slot-scope="scope">
                            <el-switch @change="setOnline(scope)" :value="scope.row.is_online" :inactive-value="0" :active-value="1" ></el-switch>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" >
                        <template slot-scope="scope">
                                <router-link :to="'/marketing/discount/edit/' + scope.row.discount_id">
                                    <el-button size="mini" type="text">编辑</el-button>
                                </router-link>&nbsp;
                                <el-popover placement="top" v-model="scope.row.is_delete">
                                    <p style="margin-bottom: 4px">确定删除吗？</p>
                                    <div style="margin: 0">
                                        <el-button :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消</el-button>
                                        <el-button :loading="loading" type="primary" size="mini" @click="del(scope)">确定</el-button>
                                    </div>
                                    <el-button type="text" size="mini" slot="reference">删除（释放产品）</el-button>
                                </el-popover>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween},
        data() {
            return {
                //整体菊花
                loading: false,
                //列表数据
                datas: [],
                //搜索数据
                searchForm: {
                    bargain_id: '',
                    goods_id: '',
                    keywords: '',
                    adddate: [],
                    startdate: [],
                    enddate: [],
                    is_online: -999,
                },
            }
        },

        created() {
            this.getList();
        },
        methods: {


            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.discount/index", {
                    appToken: this.$store.getters.appToken,
                    discount_id: this.searchForm.discount_id,
                    keywords: this.searchForm.keywords,
                    goods_id: this.searchForm.goods_id,
                    bg_add_time: this.searchForm.adddate[0],
                    end_add_time: this.searchForm.adddate[1],
                    bg_start_time: this.searchForm.startdate[0],
                    end_start_time: this.searchForm.startdate[1],
                    bg_end_time: this.searchForm.enddate[0],
                    end_end_time: this.searchForm.enddate[1],
                    is_online: this.searchForm.is_online,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },



            handleSelectionChange(val) {
                let Ids = [];
                for (let i in val) {
                    Ids.push(i);
                }
                this.discountIds = Ids;
            },



            setOnline(scope){
                this.loading = true;
                let is_online = 0;
                if (scope.row.is_online == 0) {
                    is_online = 1;
                }
                api.post("/shop/admin.marketing.discount/online", {
                    appToken: this.$store.getters.appToken,
                    discount_id: scope.row.discount_id,
                    is_online: is_online,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            del(scope) {
                this.loading = true;
                api.post("/shop/admin.marketing.discount/del", {
                    appToken: this.$store.getters.appToken,
                    discount_id : scope.row.discount_id,
                }).then(res => {
                    this.$message({
                        message: '编辑成功',
                        type: 'success'
                    });
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>

